import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../../../helpers';
import { ScriptLoaderService } from '../../../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&â€™*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./source-of-business-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class SourceOfBusinessAddComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;
	
sobForm: FormGroup;
static SerialnoMandatory ="0";
sobId = 0;  
ajaxData : SourceOfBusinessData[];
sourceofbusiness= new SourceOfBusinessData();  
config = new configDetails(); 
status;

msg = [];
bootstrapFromValues = [];

constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.sobId).subscribe(params=>{
			if(params.sobId != undefined){
				this.sobId=params.sobId; 
			}
		}); 
	if(this.sobId == 0){
		this.status = "Add";
	}else{
		this.status = "Update";
		this.populateData();
	}
	}
	
    ngOnInit() {
		this.sobForm = new FormGroup({			
			'sobId' : new FormControl(''),
			'sobName' : new FormControl('', [ Validators.required]),
			'sobEntryId' : new FormControl(''),
		    'sobEntryDate' : new FormControl(''),
			'sobModifiedId' : new FormControl(''),
			'sobModifiedDate' : new FormControl(''),
			
		});
	}
	  
	    get sobName(){ return this.sobForm.get('sobName')};
	    get sobEntryId(){ return this.sobForm.get('sobEntryId')};
	    get sobEntryDate(){ return this.sobForm.get('sobEntryDate')};
	    get sobModifiedId(){ return this.sobForm.get('sobModifiedId')};
	    get sobModifiedDate(){ return this.sobForm.get('sobModifiedDate')};
	  
	 populateData(){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
			  this.http.post<SourceOfBusinessData>(sessionStorage.getItem('requestUrl')+'/axelacrm/sales/sob-data',
							 			JSON.stringify({ sobId :this.sobId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.sourceofbusiness = data['populateData'][0];
																		console.log(this.sourceofbusiness);
			  															},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 });
	 }
 
	   ngAfterViewInit(){ }	
	
 
		 toggleAnimation(){
			this._router.navigate(['portal/source-of-business-list']);
		 }

 
	  	onSubmit() {
			this.bootstrapFromValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.sourceofbusiness[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
			if(!this.sobForm.invalid){
				  const headers = new HttpHeaders({'Content-Type':'application/json'});
			if(this.sobId == 0){ 
				this.sourceofbusiness['requestType'] = 'add';
			}else{
				this.sourceofbusiness['requestType'] = 'update';
				this.sourceofbusiness['sobId'] = this.sobId+'';
			}
			 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/sales/sob',
									JSON.stringify(this.sourceofbusiness),
						{ headers, withCredentials: true }).subscribe(data => {
															if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['errormsg'];
														}
															this.toast.showtoast(this.msg);
															this.toggleAnimation();
															this.sobForm.reset();
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		  } 
			console.log(this.sourceofbusiness);
	  }
 
  onDelete(){
		 this.sourceofbusiness['requestType'] = 'delete';
		this.sourceofbusiness['sobId'] = this.sobId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/sales/sob',
									JSON.stringify(this.sourceofbusiness),
						{ headers, withCredentials: true }).subscribe(data => {			 											
			 											if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.sobForm.reset();
//			 											this.list.getData();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
	 }
	  	
	
}


export class SourceOfBusinessData {
	
	sobId: string ;
	sobName: string;
	sobEntryId:string;
	sobEntryBy:string;
	sobEntryDate:string;
    sobModifiedId:string;
	sobModifiedBy:string;
    sobModifiedDate:string;
	
	 
}
export class configDetails {
	configCustomerSob:string;
	configCustomerSoe:string;
	configCustomerAddress:string;
}



